#nav-container{width:100%;background:#fff;position:fixed;top:0;z-index:80;display:flex;flex-direction:row}#nav-container.open{display:block}#nav-container.open nav{padding:0}@media (min-width: 860px){#nav-container{padding-top:10px}}@media (max-width: 860px){#nav-container{width:100vw;height:100%;display:none;position:fixed;top:0;left:0;padding-top:80px}}nav{font-family:"Lusitana",serif;font-size:1.4em;height:40px;padding:0 60px;flex:0 1 55vw;min-width:740px;max-width:992px;display:flex;flex-direction:row;justify-content:space-between;user-select:none}nav a{line-height:40px;color:#202d62;-webkit-transition:0.2s all;-moz-transition:0.2s all;transition:0.2s all}nav a.active{text-decoration:underline !important}nav a:link,nav a:visited{text-decoration:none}nav a:hover,nav a:active{text-decoration:underline}nav .menu-nav-menu-container{flex:1 0}nav ul{margin:0;padding:0;display:flex;flex-direction:row;justify-content:space-between}nav ul li{display:block;flex:0 1 auto;list-style-type:none}#nav-toggle{position:fixed;top:0;right:0;z-index:100;display:none;padding:8px;cursor:pointer}#nav-toggle #nav-close{display:none}@media (max-width: 860px){#nav-toggle{display:block}}.right-nav-spacer{flex:1 1 60px}.left-nav-spacer{flex:1 0 60px}
