body{color:#000;font-family:"Lato",sans-serif;font-size:14px;width:100%;height:100%;margin:0}.wp-block-image{margin:0}.aligncenter{text-align:center}.wp-block-columns{display:flex;margin:75px -20px}@media (max-width: 860px){.wp-block-columns{margin:50px 0}}.wp-block-column{flex:1;margin:0 20px}@media (max-width: 764px){.wp-block-column:first-child{flex-basis:100%}}@media (max-width: 478px){.wp-block-column{margin:20px 0}}.wp-block-image figure,.wp-block-column figure{margin:0}.wp-block-image img,.wp-block-column img{height:auto;max-width:100%}figcaption,.caption{color:#666;text-align:center;margin-top:10px;font-size:.8571em}@media (max-width: 860px){.toggle-text .hide-sm{display:none}}.toggle-text .show-toggle,.toggle-text .hide-toggle{color:#bf322f;display:none;user-select:none;cursor:pointer}@media (max-width: 860px){.toggle-text .show-toggle,.toggle-text .hide-toggle{display:inline}}
