#sidebar{background-color:#fff;box-shadow:6px 6px 4px -6px #ddd;position:fixed;top:0;left:0;z-index:90}#sidebar a:link,#sidebar a:visited,#sidebar a:hover,#sidebar a:active{text-decoration:none}@media (min-width: 860px){#sidebar{padding:14px 24px}}@media (max-width: 860px){#sidebar{width:100%;box-shadow:none}}.logo-container{position:relative;margin-right:10px;cursor:pointer}#logo{height:auto;width:50px}@media (min-width: 1400px){#logo{width:120px;margin-left:0}}@media (max-width: 860px){#logo{display:none}}#top-arrow{display:block;position:absolute;top:0;left:0;height:100%;width:100%;color:#202d62;opacity:0}#top-arrow svg{height:100%;width:100%}.name{font-family:"Lusitana",serif;color:#202d62;display:inline-block;font-size:1.4em;user-select:none;cursor:pointer;margin-top:10px}.name .studio{font-size:0.6em}@media (max-width: 860px){.name .studio{font-size:1em;text-transform:capitalize}}.name span{display:block}@media (max-width: 860px){.name span{display:inline;font-size:1em;line-height:40px}}@media (min-width: 1400px){.name{display:block;font-size:2.4em}}@media (max-width: 860px){.name{margin-left:14px}}
