html.magnifying>body{overflow-x:hidden !important}.magnify,.magnify>.magnify-lens,.magnify-mobile,.lens-mobile{min-width:0;min-height:0;animation:none;border:none;float:none;margin:0;opacity:1;outline:none;overflow:visible;padding:0;text-indent:0;transform:none;transition:none}.magnify{position:relative;width:auto;height:auto;box-shadow:none;display:inline-block;z-index:inherit}.magnify>.magnify-lens{position:absolute;width:500px;height:500px;cursor:crosshair;display:none;z-index:99}.magnify>.magnify-lens.loading{background:#333 !important;opacity:0.8}.magnify>.magnify-lens.loading:after{position:absolute;top:50%;left:0;width:100%;color:#fff;content:'Loading...';letter-spacing:1px;margin-top:-8px;text-align:center;text-shadow:0 0 2px rgba(51,51,51,0.8);text-transform:none}
