.wp-block-latest-posts{list-style-type:none;display:flex;flex-wrap:wrap;justify-content:space-between;padding:0;margin:40px -20px 20px -20px}.wp-block-latest-posts li{display:flex;flex:0 0 25%;flex-direction:column;margin:0 20px 60px 20px}.wp-block-latest-posts li>a{flex:0 0 60px;font-size:1.1429em;font-weight:bold;color:#000}.wp-block-latest-posts li>a:link{text-decoration:none}.wp-block-latest-posts li time{flex:0;padding-bottom:20px;color:#666;font-size:.8571em}.wp-block-latest-posts li .wp-block-latest-posts__post-excerpt{flex:1}
